import React from 'react'
import ReactDOM from 'react-dom'
import './index.css'
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import App from './App'
import Skyscraper from './pages/Skyscraper/Skyscraper'
import Teapot from './pages/Teapot/Teapot'
import Sudoku from './pages/Sudoku/Sudoku'
import createHistory from 'history/createBrowserHistory';

const history = createHistory({
    basename: process.env.PUBLIC_URL,
  });

ReactDOM.render(
    <BrowserRouter history={history} basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route exact path="/" component={() => <App filter={"all"} />} />
          <Route path="/code" component={() => <App filter={"code"} />} />
          <Route path="/design" component={() => <App filter={"design"} />} />
          <Route path="/skyscraper" component={Skyscraper} />
          <Route path="/sudoku" component={Sudoku} />
          <Route path="/teapots" component={Teapot} />
          <Route component={() => (<div>404 Not found </div>)} />
        </Switch>
    </BrowserRouter>
    , document.getElementById('root'));