import React from 'react';
import '../page.css';

import Titlebar from '../../components/Titlebar/Titlebar';
import teapot1 from '../../images/teapot/teapot1.png';
import teapot2 from '../../images/teapot/teapot2.png';
import teapot3 from '../../images/teapot/teapot3.gif';

class Teapot extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			pageNumber: 0,
			teapots: [teapot1, teapot2, teapot3],
		}
		console.log(this.state)
	}

	pageHandler = () => {
		let newPage;
		if (this.state.pageNumber === 2) newPage = 0
		else newPage = this.state.pageNumber + 1
		this.setState({
			pageNumber: newPage,
		})
	}

	dotHandler = (idx) => {
		this.setState({
			pageNumber: idx,
		})
	}

	render() {
		const text1 = <p style={{ marginBottom: 0 }}>
				 		<b>Teapot Iterations<br />
				 		ARC374: Computational Design</b><br />
				 		<i>Grasshopper, Rhino, AfterEffects</i><br /><br />
				    	Inspired by the iconic Utah Teapot, this semester-long project involved designing a
				    	system of teapots in Grasshopper. By varying parameters like angle of rotation and by
				    	tweaking the form of a bezier curve, thousands of unique teapots can be generated from a
				    	single system.
			    	</p>
		const text2 = <p style={{ marginBottom: 0 }}>
									<b>Teapot Iterations<br />
				 					ARC374: Computational Design</b><br />
				 					<i>Grasshopper, Rhino, AfterEffects</i><br /><br />
				 					This system originated with the idea of reducing the curves of the Utah teapot into 
				 					polygonal shapes. Each part of the teapot is formed with its own Bezier curve, which is then 
				 					segmented and rotated. By varying the shape of the starting curve, the number of divisions and 
				 					the angles of rotation, a series of distinct teapots are formed. The series on the left is
				 					color-coded according to number of segments, where orange corresponds with 3 segments and pink
				 					corresponds with 12.

									</p>
		const text3 = <p style={{ marginBottom: 0 }}>
									<b>Teapot Iterations<br />
				 					ARC374: Computational Design</b><br />
				 					<i>Grasshopper, Rhino, AfterEffects</i><br /><br />
									This construction sequence elucidates the process of creating the teapot system, exposing the
									each teapot's inner scaffolding. Again, the colors reiterate the complexity of the Bezier curve
									segmentation. Made using Adobe AfterEffects.
									</p>
		const texts = [text1, text2, text3]
		const video = <video className="portfolio-img" autoPlay="autoplay" onClick={this.pageHandler} controls loop>
									  <source src={require("../../images/teapot/teapot3.mp4")} type="video/mp4" />
									</video>
	  return (
	    <div>
	    	<div className="titlebar-container"><Titlebar /></div>
	    	<div className='portfolio-container'>
		    	<div className='image-container'>
		    		{<img src={this.state.teapots[this.state.pageNumber]} className="portfolio-img" onClick={this.pageHandler}/> }
		    	</div>
		    	<div className='text-container'>
		    		{texts[this.state.pageNumber]}
		   		</div>
		    </div>
		    <div className="dot-container">
		   			<span className={"dot" + (this.state.pageNumber === 0 ? ' selected' : '')} onClick={() => {this.dotHandler(0);}}></span>
		   			<span className={"dot" + (this.state.pageNumber === 1 ? ' selected' : '')} onClick={() => {this.dotHandler(1);}}></span>
		   			<span className={"dot" + (this.state.pageNumber === 2 ? ' selected' : '')} onClick={() => {this.dotHandler(2);}}></span>
		   	</div>
	    </div>
	  );
	}
}

export default Teapot;
