import React, { useRef, useEffect } from 'react';
import '../page.css';

import Titlebar from '../../components/Titlebar/Titlebar';
import sudoku1 from '../../images/sudoku/sudoku2.gif';
import sudoku2 from '../../images/sudoku/sudoku.gif';

var Preload = require('react-preload').Preload;

class Sudoku extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pageNumber: 0,
      sudokus: [sudoku1, sudoku2]
    }
  }

  pageHandler = () => {
    let newPage;
    if (this.state.pageNumber === 1) newPage = 0
    else newPage = this.state.pageNumber + 1
    this.setState({
      pageNumber: newPage,
    })
  }

  dotHandler = (idx) => {
    this.setState({
      pageNumber: idx,
    })
  }

  render() {
    const text1 = <p style={{ marginBottom: 0 }}>
                    <b>NYT Sudoku</b><br />
                    <i>React, SCSS</i><br /><br />
                    While interning on the New York Times' Game Team this summer,
                    I worked on migrating the current Sudoku game from Backbone to React.
                    This migration aimed to make the game mobile friendly and responsive.
                  </p>
    const text2 = <p style={{ marginBottom: 0 }}>
                    <b>NYT Sudoku</b><br />
                    <i>React, SCSS</i><br /><br />
                    One of the key features that I implemented was the Sudoku keyboard. This
                    was an important step in making the game mobile-friendly. The keyboard allows
                    the player to enter numbers in both Normal and Candidate modes. It also allows
                    the toggling of auto-candidate mode, which automatically enters and updates possible
                    candidates in every blank square.
                  </p>
    const texts = [text1, text2]
    return (
      <div>
      	<div className="titlebar-container"><Titlebar /></div>
      	<div className='portfolio-container'>
  	    	<div className='image-container'> 
            <Preload
                images={this.state.sudokus}
                autoResolveDelay={3000}
                onError={this._handleImageLoadError}
                onSuccess={this._handleImageLoadSuccess}
                resolveOnError={true}
                mountChildren={true}
            >
                {<img src={this.state.sudokus[this.state.pageNumber]} className="portfolio-img" onClick={this.pageHandler} />}
            </Preload>
  	    	</div>
  	    	<div className='text-container' >
            {texts[this.state.pageNumber]}
  	    	</div>
  	    </div>
        <div className="dot-container">
          <span className={"dot" + (this.state.pageNumber === 0 ? ' selected' : '')} onClick={() => {this.dotHandler(0);}}></span>
          <span className={"dot" + (this.state.pageNumber === 1 ? ' selected' : '')} onClick={() => {this.dotHandler(1);}}></span>
        </div>
      </div>
    );
  }
}

export default Sudoku;
