import React from 'react';
import './Grid.css';

import p2d from '../../images/p2d/p2d.svg'
import skyscraper from '../../images/skyscraper/skyscraper.svg'
import sudoku from '../../images/sudoku/sudoku.svg'
import stela from '../../images/stela/stela.svg'
import qstyle from '../../images/style/style.svg'
import teapot from '../../images/teapot/teapot.svg'

import { Link } from 'react-router-dom';

const Grid = (props, context) => {
  const hideCode = !(props.filter === "all" || props.filter === "code")
  const hideDesign = !(props.filter === "all" || props.filter === "design")
  const hideStyle = { display: 'none' }
  const showStyle = { display: '' }
  return (
    <div className="grid">
      <a href='/p2d' className='grid-link'>
        <div className='grid-item p2d'>
          <img src={p2d} className="grid-img" />
          <div className='grid-text'>PressToDress</div>
        </div>
      </a>
      <a href={'/skyscraper'} className='grid-link' style={hideCode ? hideStyle : showStyle}>
        <div className='grid-item skyscraper'>
          <img src={skyscraper} className="grid-img" />
          <div className='grid-text'>NYT Skyscraper</div>
        </div>
      </a>
      <a href='/sudoku' className='grid-link' style={hideCode ? hideStyle : showStyle}>
        <div className='grid-item sudoku'>
          <img src={sudoku} className="grid-img" />
          <div className='grid-text'>NYT Sudoku</div>
        </div>
      </a>
      <a href='/stela' className='grid-link' style={hideCode ? hideStyle : showStyle}>
        <div className='grid-item stela'>
          <img src={stela} className="grid-img" />
          <div className='grid-text'>NYT StelaDual</div>
        </div>
      </a>
      <a href='/teapots' className='grid-link' style={hideDesign ? hideStyle : showStyle}>
        <div className='grid-item teapot'>
          <img src={teapot} className="grid-img" />
          <div className='grid-text'>Teapot Iterations</div>
        </div>
      </a>
      <a href='/qstyle' className='grid-link' style={hideCode ? hideStyle : showStyle}>
        <div className='grid-item qstyle'>
          <img src={qstyle} className="grid-img" />
          <div className='grid-text'>Quantifying Style</div>
        </div>
      </a>
        <div className='grid-item thesis'>
          <div className='grid-text'>Princeton Senior Thesis<br /> In Progress</div>
        </div>
    </div>
  );
}

